<template>
    <div>
        <h1 id="title" style="color:#111e2c;"><b style="color:#96ff54;">GALERIA</b> <b>DE SÉRIES</b></h1>
        <div class="container filters" v-if="this.movies != 0">
            <input type="text" v-model="searchTxt" id="searchArea" placeholder="Pesquisar...">
            <button class="btn btn-primary" @click="alphabeticalOrder()">{{alphabet}}</button>
            <button @click="clear()" class="btn btn-primary">X</button>
        </div>
        <div v-else>
            <h1 id="title"><b style="color:#96ff54;">X</b> <b>Não existem séries registadas</b> <b
                    style="color:#96ff54;">X</b></h1>
            <button class="btn btn-primary add">
                <router-link id="link" to="/register">+ ADICIONAR SÉRIE</router-link>
            </button>
        </div>

        <div class="container" v-if="this.movies != 0" style="margin-bottom:50px;">
            <div class="row" >
                <div class="col-sm-3"  v-for="movie in filteredMovies" :key="movie.id" v-if="movie.type === 'serie'" >
                        <div class="card"  style="color:black;">
                        <img class="card-img" :src="movie.image">
                        <div class="card-body">
                            <h5 class="cardName">{{movie.name}}</h5>
                            <button @click="remove(movie.id)" class="btn btn-primary deleteBtn">X</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data: function () {
            return {
                movies: [],
                alphabet: "A-Z",
                compareType: false,
                searchTxt: "",
                min: 0,

            }
        },
        created() {
            if (localStorage.getItem("movies")) {
                this.$store.state.movies = JSON.parse(localStorage.getItem("movies"))
            }
            this.movies = this.$store.state.movies

            this.movies[28].name = "Emperor's New Groove"
            localStorage.setItem("movies", JSON.stringify(this.movies));


        },
        methods: {

            compareNameAZ(a, b) {
                if (a.name < b.name) return -1
                if (a.name > b.name) return 1
                else return 0
            },
            compareNameZA(a, b) {
                if (a.name > b.name) return -1
                if (a.name < b.name) return 1
                else return 0
            },
            alphabeticalOrder() {
                if (this.compareType === true) {
                    this.movies.sort(this.compareNameZA)
                    this.alphabet = "A-Z"
                    this.compareType = false
                } else if (this.compareType === false) {
                    this.movies.sort(this.compareNameAZ)
                    this.alphabet = "Z-A"
                    this.compareType = true
                }
            },
            normalOrder(a, b) {
                let first = parseInt(a.id)
                let second = parseInt(b.id)
                return first > second ? 1 : second > first ? -1 : 0;
            },
            clear() {
                this.movies = this.$store.state.movies
                this.movies.sort(this.normalOrder)
                this.searchTxt = ""
            },
            remove(id) {

                if (confirm("Deseja Remover?")) {
                    for (let i in this.movies) {

                        if (this.movies[i].id === id) {
                            this.movies = this.movies.filter(movie => this.movies[i].id != movie.id);
                            localStorage.setItem("movies", JSON.stringify(this.movies));
                            alert("Removido!")
                        }
                    }
                }

            }
        },
        computed: {
            filteredMovies() {
                return this.movies.filter(
                    (movie) => {
                        let filterResult = true

                        if (this.searchTxt == "") {
                            return filterResult
                        }
                        if ((movie.name).toLowerCase().includes((this.searchTxt).toLowerCase())) {
                            filterResult = (movie.name).toLowerCase().includes((this.searchTxt).toLowerCase())
                            return filterResult
                        }
                    }
                )

            }
        }
    }
</script>

<style lang="css" scoped>
    #title {
        padding-top: 100px;

        padding-bottom: 50px;
        font-size: 70px;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

    }


    .filters {
        margin-bottom: 30px;
    }

    .btn {
        margin-left: 10px;
        background-color: #96ff54;
        border: solid 1px #111e2c;
        color: #111e2c;
        font-weight: bold;
        border-radius: 0px;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;


    }

    .deleteBtn {
        background-color: transparent;
        color: transparent;
        margin-left: 190px;
        font-size: 10px;
        margin-top: -780px;
        border: solid 0px #111e2c;

    }

    #link {
        color: #111e2c;
    }

    #searchArea {
        height: 35px;
        margin-bottom: 30px;
    }

    .home {
        margin-top: 20px;
        color: black;
        width: 100px;
        margin-bottom: 50px;
    }

    .filter {
        background-color: transparent;
        margin: 0;
        border: solid 1px transparent;
        font-weight: normal;
        margin-right: -10px;

    }

    .btn:hover {
        background-color: #111e2c;
        border: solid 1px #96ff54;
        border-radius: 0px;
        color: white;

    }
    .wacthBtn{
        color: transparent;
        font-weight: bold;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    }

    .wacthBtn:hover {
        color: #111e2c;

    }


    .btn:hover>#link {
        color: white;
    }

    .cardName {
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        font-size: 16px;
    }

    .card-body {
        height: 70px;
        border-top: 5px solid #111e2c;
    }

    .card {
        border: 0px solid rgb(223, 223, 223);
