        width: 16rem;
        margin-bottom: 50px;
        box-shadow: -4px 4px 5px -1px rgba(0, 0, 0, 0.15);
    }

    .card-img {
        height: 350px;
        border-radius: 0px;

    }

    .card:hover {
        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    }

    
    .card:hover>.card-body {
        border-top: 5px solid #96ff54;
    }

    @keyframes scale-up-center {
        0% {
            transform: scale(1);
        }

        100% {
            transform: scale(1.1);
        }
    }

      .col-sm-3 {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
</style>